@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700&display=swap);
body {
  margin: 0;
  padding: 0;
  background-color: #f3f3f3;
  font-family: "Montserrat";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
a {
  text-decoration: none;
  color: inherit;
}

.content {
  margin: 0 auto;
  padding-left: 325px;
  max-width: 1600px;
}
.flex-wrapper {
  /* margin: 0 50px; */
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.button {
  padding: 10px;
  background-color: #fff;
  font-size: 14px;
  border-radius: 5px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  font-weight: 700;
  margin-right: 10px;
  transition: 0.2s all;
  opacity: 1;
  border: none;
}
.button:hover {
  opacity: 0.8;
  transition: 0.2s all;
}

.blue-bg {
  color: #fff;
  background-color: #668fa9;
}
.green-bg {
  color: #fff;
  background-color: #4bae6f;
}
.wrap {
  margin: 50px 50px;
}

/* 275 */
.menu {
  position: fixed;
  width: 275px;
  height: 100%;
  left: 0;
  top: 0;
  background-color: #2c2c2c;
  color: #ffffff;
}
ion-icon {
  font-size: 0.9em;
  margin-right: 5px;
}
.menu__user {
  margin-top: 20px;
  margin-left: 50px;
  width: 180px;
  padding-bottom: 8px;
  border-bottom: 1px solid #cccccc;
  margin-bottom: 40px;
}
.menu__user__name {
  font-size: 16pt;
  font-weight: 700;
  line-height: 1;
}
.menu__user__email {
  font-size: 10pt;
  font-weight: 300;
  margin-top: -1px;
}

.menu__item {
  margin-left: 50px;
  font-size: 14px;
  margin-bottom: 10px;
}

.menu__item a {
  color: #ffffff;
  text-decoration: none;
}
.menu__item a span {
  color: #ffffff;
  text-decoration: none;
}
.menu__item a:hover {
  transition: 0.2s all;
  color: #cccccc;
}
.menu__item_active a {
  color: #668fa9;
}

.header {
  font-weight: 700;
  font-size: 32px;
}
.header__description {
  margin-top: -10px;
  margin-right: 50px;
}
.header_size_m {
  font-size: 28px;
}

.status {
  position: absolute;
  top: 20px;
  right: 20px;
  font-weight: 700;
  font-size: 12px;
}
.status_available {
  color: #668fa9;
}
.status_not_available {
  color: red;
}
.status_complated {
  color: green;
}
.status_performed {
  color: yellow;
}

.card {
  position: relative;
  width: 370px;
  height: 200px;
  background: #fff;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  border-radius: 10px;
  padding: 5px 25px 15px;
  margin: 20px;
}
.card__title {
  font-size: 20px;
  border-bottom: 1px solid #e7e7e7;
  padding: 0 0 10px 0;
}
.card__description {
  font-size: 14px;
  font-weight: 400;
  margin-top: -5px;
}
.card__button {
  position: absolute;
  bottom: 20px;
  background-color: #668fa9;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  color: #fff;
  font-size: 14px;
  cursor: pointer;
  transition: 0.2s all;
  opacity: 1;
}
.card__button:hover {
  opacity: 0.9;
  transition: 0.2s all;
}

.card__status {
  position: absolute;
  top: 25px;
  right: 20px;
  font-weight: 700;
  font-size: 10px;
}

.modal {
  position: fixed;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  display: block;
  display: flex;
  justify-content: center;
  -webkit-animation: show 0.5s;
          animation: show 0.5s;
}
.modal__content {
  width: 70%;
  margin: 50px 0;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  border-radius: 10px;
  -webkit-animation: slide 0.5s;
          animation: slide 0.5s;
  overflow: auto;
}
.hidden {
  display: none;
}
.modal__close {
  position: fixed;
  width: 41px;
  height: 41px;
  top: 40px;
  right: 20px;
  color: #fff;
  font-size: 45px;
}

@-webkit-keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
    left: 0;
  }
}

@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
    left: 0;
  }
}
@-webkit-keyframes slide {
  from {
    margin-left: -100%;
  }
  to {
    margin-left: 0;
  }
}
@keyframes slide {
  from {
    margin-left: -100%;
  }
  to {
    margin-left: 0;
  }
}

.task {
  background-color: #ffffff;
  margin: 20px 50px 0 50px;
  border-radius: 10px;
  background: #fff;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  padding: 5px 25px 15px;
  position: relative;
}
.task__title {
  border-bottom: 1px solid #f3f3f3;
}
.task__description {
  font-weight: 300;
  font-size: 12px;

  margin-top: -20px;
  padding: 10px 0;
  border-bottom: 1px solid #f3f3f3;
}

.comment {
  background-color: #fff;
  margin: 20px 0;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
}
.comment__link {
  color: blue;
  display: block;
}
.comment__link:hover {
  text-decoration: underline;
}

.form-project {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.form-project__block {
  border-bottom: 1px solid #ccc;
}
.form-project__description {
  width: 30%;
}
.form-project__description h2 {
  margin: 0;
  padding: 0;
}
.form-project__inputs {
  width: 60%;
  padding: 30px;
  background-color: #fff;
  border-radius: 10px;
  margin-bottom: 20px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
}
.form-project__inputs__label {
  font-size: 12px;
  margin: 0;
}
.form-project__inputs__input {
  padding: 5px 0 5px 5px;
  border-radius: 5px;
  box-shadow: none;
  border: 1px solid #ccc;
  width: 100%;
  font-size: 14px;
  font-weight: 700;
}
.form-project__inputs__block {
  margin-bottom: 15px;
}

.form-project__inputs__select {
  margin-right: 10px;
  background-color: #fff;
}
.form-project__inputs__select option {
  margin-right: 10px;
  background-color: #fff;
}

.form-project__inputs__select {
  border: 1px solid #ccc;
  font-weight: 700;
  font-size: 14px;
  border-radius: 5px;
  padding: 5px 0 5px 5px;
  margin-bottom: 10px;
}
.form-project__inputs__select option {
  font-weight: 700;
  padding: 10px;
  margin: 10px;
}



.nav-button {
  display: inline-block;
  padding: 10px;
  background-color: #fff;
  font-size: 14px;
  border-radius: 5px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  font-weight: 700;
  margin-right: 10px;
  margin-bottom: 10px;
  transition: 0.2s all;
  opacity: 1;
  border: none;
}
.nav-button:hover {
  opacity: 0.8;
  transition: 0.2s all;
}

